import { css } from 'styled-components'
import { fonts, fontSizes } from '@theme/styles'
import mediaMin from '@theme/media'

const mixins = {
  muli: css`
    font-family: ${fonts.muli};
    letter-spacing: 0.03em;
    line-height: 150%;
    font-size: ${fontSizes.md};
  `,
  teko: css`
    font-family: ${fonts.teko};
    font-size: ${fontSizes.xxl};
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 300;
  `,
  roboto: css`
    font-family: ${fonts.roboto};
    font-size: ${fontSizes.md};
    letter-spacing: 0.01rem;
  `,
  inlineLink: css`
    text-decoration: none;
    color: ${({ theme }) => theme.onBackground};
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    box-shadow: inset 0 -0.2rem ${({ theme }) => theme.primary};
    &:hover {
      color: ${({ theme }) => theme.onBackgroundHover};
      box-shadow: inset 0 -0.7rem ${({ theme }) => theme.primary};
    }
  `,
  grid: (col?: string) => {
    return css`
      display: grid;
      grid-template-columns: 1fr;
      grid-column-gap: 3rem;
      grid-row-gap: 2rem;
      ${mediaMin.tablet`grid-template-columns: ${col || 'repeat(2, 1fr)'}`}
    `
  },
  contentSpace: css`
    ${mediaMin.laptop`
      max-width: 800px;
      margin: 0 auto;
      padding: 0;`}

    ${mediaMin.desktop`
      max-width: 1000px;
      margin: 0 auto;
  `}
  `,
  heading: css`
    font-family: ${fonts.muli};
    font-size: ${fontSizes.xxl};
    color: ${({ theme }) => theme.onBackgroundHover};
    ${mediaMin.laptop`font-size: ${fontSizes.xxxxl};`}
  `,
  flexCenter: css`
    display: flex;
    justify-content: center;
    align-items: center;
  `,
  flexBetween: css`
    display: flex;
    justify-content: space-between;
    align-items: center;
  `,
  transitionAll: css`
    transition: all 0.3s ease-in-out;
  `,
}

export default mixins
